@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,400|Roboto:300,300i,400&display=swap);
html {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

*, *:before, *:after {
	box-sizing: inherit;
}

html, body, #root {
	min-width: 100vw;
	min-height: 100vh;
	overflow-x: hidden;
}

body {
	margin: 0;
	height: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body:before, body:after {
  display: none;
}

h1 {
	font-size: 150px;
	font-family: 'Raleway', sans-serif;
	font-weight: 100;
	opacity: 0.15;
}

.section {
	overflow-y: scroll;
	overflow-x: hidden;
	min-height: 100vh;
	text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
	-webkit-animation: image-load 2.5s;
	        animation: image-load 2.5s;
}

.header {
	font-family: 'Raleway', sans-serif;
	font-weight: 300;
}

.header-overlay {
	width: 100%;
	height: 70px;
	z-index: 50;
	position: fixed;
	background: rgba(255, 255, 255, 0);
	-webkit-transition: all 300ms ease-in-out;
	transition: all 300ms ease-in-out;
	display: none;
}

.header-overlay.header-transparent {
	background: rgba(255, 255, 255, 0);
}

.header-mobile {
	display: none;
}

.header-desktop {
	z-index: 50;
	position: fixed;
	display: flex;
	width: 100%;
	padding: 8px;
	background: rgba(255, 255, 255, 0);
	-webkit-transition: all 300ms ease-in-out;
	transition: all 300ms ease-in-out;
}

.header-desktop.header-background {
	width: auto;
	height: 100%;
}

.header-desktop.header-transparent {
	background: rgba(255, 255, 255, 0);
}

.header-desktop nav {
	width: 100%;
}

.header-desktop.header-background nav .vertical-divider {
	border-right: 1px solid #8a8a8a;
	height: 100%;
}

.header-desktop nav ul {
	display: flex;
	width: 100%;
	justify-content: space-around;
	-webkit-transition: all 500ms ease-in-out;
	transition: all 500ms ease-in-out;
}

.header-desktop.header-background nav ul {
	display: block;
	width: auto;
	height: 100%;
	-webkit-padding-start: 0;
	        padding-inline-start: 0;
	padding: 16px;
}

.header-desktop nav ul li {
	list-style: none;
	padding: 0 16px;
}

.header-desktop.header-background nav ul li {
	padding: 16px 0;
}

.header-desktop nav ul.white-text {
	border-left: 1px solid #3F3838;
}

.header-desktop nav ul.white-text li a {
	color: #8a8a8a;
}

.header-desktop nav ul.white-text li a:link {
	color: #8a8a8a;
}

.header-desktop nav ul.white-text li a:active {
	color: #8a8a8a;
}

.header-desktop nav ul.white-text li a:visited {
	color: #8a8a8a;
}

.header-desktop nav ul.white-text li a:hover {
	color: white;
}

.header-desktop nav ul.black-text {
	border-left: 1px solid #BEBEBE;
}

.header-desktop nav ul.black-text li a {
	color: #373a47;
}

.header-desktop nav ul.black-text li a:link {
	color: #373a47;
}

.header-desktop nav ul.black-text li a:active {
	color: #373a47;
}

.header-desktop nav ul.black-text li a:visited {
	color: #373a47;
}

.header-desktop nav ul.black-text li a:hover {
	color: black;
}

.header-desktop nav ul li a {
	text-decoration: none;
	font-size: 14px;
	font-weight: 100;
	text-transform: uppercase;
	letter-spacing: 1.5px;
	text-shadow: none;
	-webkit-transition: all 0.30s ease-in-out;
	-moz-transition: all 0.30s ease-in-out;
	-ms-transition: all 0.30s ease-in-out;
	-o-transition: all 0.30s ease-in-out;
}

.header-desktop nav ul li a span {
	text-transform: lowercase;
}

button.button {
	background: -webkit-gradient(linear, right top, left top, from(rgba(234, 234, 234, 0.1)), to(rgba(138, 138, 138, 0.1)));
	background: linear-gradient(to left, rgba(234, 234, 234, 0.1), rgba(138, 138, 138, 0.1));
	outline: 0;
	border: none;
	padding: 12px 16px;
	cursor: pointer;
	box-shadow: none;
	transition: all 0.30s ease-in-out;
	-webkit-transition: all 0.30s ease-in-out;
	-moz-transition: all 0.30s ease-in-out;
	-ms-transition: all 0.30s ease-in-out;
	-o-transition: all 0.30s ease-in-out;
}

button.button:hover {
	background: -webkit-gradient(linear, right top, left top, from(rgba(234, 234, 234, 0.2)), to(rgba(138, 138, 138, 0.2)));
	background: linear-gradient(to left, rgba(234, 234, 234, 0.2), rgba(138, 138, 138, 0.2));
}

button.button a {
	font-size: 14px;
	letter-spacing: 1.2px;
	text-transform: uppercase;
	font-weight: 400;
	text-decoration: none;
	color: #8a8a8a;
	font-family: 'Roboto', sans-serif;
	transition: all 0.30s ease-in-out;
	-webkit-transition: all 0.30s ease-in-out;
  	-moz-transition: all 0.30s ease-in-out;
  	-ms-transition: all 0.30s ease-in-out;
	-o-transition: all 0.30s ease-in-out;
}

button.button:hover a {
	color: white;
}

@-webkit-keyframes image-load {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes image-load {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@media (max-width: 1400px) {
	.header-desktop {
		display: none;
	}
	.header-mobile {
		display: block;
	}
	.header-overlay {
		display: block;
		height: 48px;
	}
	.header-mobile.header-background {
		width: 100%;
		height: 100%;
	}
	.gallery-title, .about-title, .collections-title, .contact-title, .artworks-title {
		padding-top: 30px;
	}
}

