	}
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 22px;
  height: 18px;
  left: 16px;
  top: 16px;
}

.bm-burger-button button {
	outline: 0;
}

.bm-burger-bars {
	opacity: 0.4 !important;
	height: 1px !important;
}

/* Color/shape of burger icon bars */
.header-mobile.white .bm-burger-bars {
	background: #8a8a8a;
}

.header-mobile.black .bm-burger-bars {
	background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 30px;
  width: 30px;
}

.bm-cross-button button {
	outline: 0;
}

/* Color/shape of close button cross */
.bm-cross {
	background: -webkit-gradient(linear, left top, left bottom, from(#eaeaea), to(#8A8A8A));
	background: linear-gradient(#eaeaea, #8A8A8A);
	opacity: 0.4;
	width: 1px !important;
	height: 24px !important;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
	background: rgba(25, 24, 26, 0.8);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list a {
	color: #8a8a8a;
	text-decoration: none;
	font-size: 14px;
	font-weight: 100;
	text-transform: uppercase;
	letter-spacing: 1.5px;
	padding: 16px 0;
	outline: 0;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


@-webkit-keyframes glow {
  from {
    text-shadow: 0 2px 4px rgba(234, 234, 234, .2);
