.wrapper {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 2px;
          perspective: 2px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}

.modal-main {
  position:fixed;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.close {
  position: absolute;
  z-index: 10;
  width: 32px;
  height: 32px;
  opacity: 0.1;
  top: 24px;
  right: 24px;
  cursor: pointer;
}

.close:hover {
  opacity: 0.3;
}

.close:before, .close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#8a8a8a));
  background: linear-gradient(#ffffff, #8a8a8a);
}

.close:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.close:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

body .display-block {
  overflow: hidden;
}

.display-block {
  opacity: 1;
  z-index: 200;
}

.display-none {
  opacity: 0;
  z-index: -1;
}

@media (max-width: 1023px) {
  .modal {
    display: none;
  }
}
.loader,
.loader:before,
.loader:after {
  background: #c0c0c0;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 30px;
}
.loader {
  color: #c0c0c0;
  text-indent: -9999em;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 8px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
